/** Media Queries **/
.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, #5bd9f8 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .acciones a {
  color: #0891B2;
}

